___CSS_LOADER_EXPORT___.push([module.id, `/* GENERAL */

:root {
	--white: #fcfcfc;
	--black: #202020;
	--gray: #777777;
	--light-gray: #f5f5f5;
	--High-priority: #e72929;
	--Medium-priority: #fb9048;
	--Low-priority: #87a2ff;

	--border-radius-btns: 10px;
}

button {
	background: none;
	border: none;
	outline: 0px;
	cursor: pointer;
	transition: all 200ms ease;
}

button:hover {
	transition: all 200ms ease;
}

.material-symbols-rounded {
	font-weight: 300;
	font-size: 2rem;
}

li {
	display: flex;
	align-items: center;
}

/* CONTAINER */

.container {
	display: flex;
	width: 100vw;
	height: 100vh;
	padding: 8px;
	background: var(--white);
	overflow-x: hidden;
	font-family: "DM Sans", Helvetica, sans-serif;
}

/* SIDEBAR */

.sidebar {
	display: flex;
	flex-direction: column;
	background: var(--light-gray);
	padding: 15px 0px;
	width: 0px;
	gap: 40px;
	border-radius: 15px;
	overflow: hidden;
	transition: all 200ms ease;
}

.sidebar h1,
#add-new-project-btn > * {
	flex-shrink: 0;
	word-break: keep-all;
}

.sidebar h1 {
	font-size: 1.6rem;
	font-weight: 500;
}

.sidebar.open {
	flex: 1;
	padding: 15px 25px;
	transition: all 200ms ease;
}

@media (max-width: 750px) {
	.sidebar {
		z-index: 2;
	}

	.sidebar.open {
		position: absolute;
		height: 98%;
		width: 65%;
	}
}

.sidebar div:nth-child(1) {
	display: flex;
	justify-content: space-between;
	padding: 0;
}

#sidebar-toggle-btn {
	display: flex;
	position: relative;
	transition: all 200ms ease;
}

#sidebar-toggle-btn.closed {
	animation: return 200ms ease-out;
	position: absolute;
	top: 20px;
	left: 8px;
	transition: all 200ms ease;
}

@keyframes return {
	0% {
		left: 150px;
	}
	100% {
		left: 8px;
	}
}

.default-sidebar-list {
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.sidebar-projects h2 {
	font-size: 1.2rem;
	font-weight: 500;
}

.sidebar-project-btn,
.project-btn {
	width: 100%;
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 3px 0px;
	gap: 8px;
	font-weight: 400;
	font-size: 1.1rem;
	border-radius: 5px;
}

.sidebar-project-btn:active {
	scale: 1.02;
}

.sidebar-project-btn:hover,
.project-btn:hover {
	background: #f1f1f1;
}

#add-new-project-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 5px;
	width: 100%;
	height: 40px;
	margin: 15px 0px 0px 0px;
	background: #e4e4e4;
	border-radius: var(--border-radius-btns);
	color: var(--gray);
	font-size: 1rem;
	font-weight: 600;
	transition: background-color 100ms ease;
}

#add-new-project-btn:hover {
	transition: background-color 100ms ease;
	background: var(--black);
	color: var(--white);
}

#add-new-project-btn:active {
	scale: 1.02;
}

.projects-sidebar-list {
	display: flex;
	gap: 5px;
	flex-direction: column;
	margin-top: 10px;
	width: 100%;
}

.projects-sidebar-list li {
	width: 100%;
}

button.project-btn {
	width: 100%;
}

/* MAIN TODO CONTENT */

.main-to-do-list-container {
	display: flex;
	flex-direction: column;
	gap: 20px;
	flex: 4;
	padding: 25px 80px;
	background: var(--white);
	transition: all 200ms ease;
}

.main-to-do-list-container div:nth-child(1) {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.main-to-do-list-container.closed {
	padding: 25px 120px;
	transition: all 200ms ease;
}

@media (max-width: 750px) {
	.main-to-do-list-container {
		padding: 60px 10px;
	}

	.main-to-do-list-container.closed {
		padding: 60px 10px;
	}
}

ul#to-do-list {
	display: flex;
	flex-direction: column;
	gap: 5px;
	padding: 0;
	overflow: hidden;
}

#project-name {
	font-size: clamp(1.2rem, 2vw + 2rem, 3rem);
	color: var(--black);
	padding: 0;
	line-height: 1;
}

#to-dos-length {
	margin-top: -8px;
	font-weight: 400;
	color: var(--gray);
}

.division-line {
	width: 100%;
	background: var(--gray);
	opacity: 0.2;
	height: 1px;
}

.to-do-item {
	position: relative;
	display: flex;
	left: 0;
	align-items: center;
	gap: 20px;
	cursor: pointer;
	height: 40px;
	padding-left: 10px;
	border-radius: 5px;
	border: 1px solid var(--light-gray);
	transition: background-color 100ms ease, left 1s ease, opacity 1s ease;
	opacity: 1;
}

@media (max-width: 750px) {
	.to-do-item {
		height: 60px;
	}
}

.to-do-item.completed {
	left: 100px;
	opacity: 0;
}

.to-do-item:hover {
	transition: all 200ms ease;
	background: var(--light-gray);
}

#new-to-do-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 5px;
	width: 150px;
	height: 40px;
	margin: 0;
	background: var(--black);
	border-radius: 8px;
	color: var(--white);
	font-size: 1rem;
	font-weight: 600;
	transition: background-color 100ms ease;
}

#new-to-do-btn:hover {
	transition: background-color 100ms ease;
	background: var(--white);
	color: var(--black);
	border: 2px solid var(--light-gray);
}

#new-to-do-btn:active {
	scale: 1.02;
}

input[type="checkbox"] {
	appearance: none;
	width: 20px;
	height: 20px;
	background-color: #fff;
	border: 1px solid #ccc;
	border-radius: 5px;
	cursor: pointer;
}

input[type="checkbox"]:checked::after {
	content: "\\e834";
	font-family: "Material Icons";
	font-size: 24px;
	color: var(--black);
	position: relative;
	border-radius: 15px;
	top: -9px;
	left: -3px;
	opacity: 1;
	transition: opacity 0.2s ease-in-out;
}

.to-do-date-text {
	display: flex;
	align-items: center;
	margin-left: auto;
	color: var(--gray);
}

.to-do-priority {
	display: flex;
	align-items: center;
	transition: all 200ms ease-in-out;
	cursor: pointer;
}

.to-do-priority:hover {
	transition: all 200ms ease-in-out;
	scale: 1.09;
	transition: all 200ms ease-in-out;
	scale: 1.09;
}

.to-do-priority:active {
	transition: all 200ms ease-in-out;
	scale: 1.2;
}

.to-do-priority i {
	margin-top: 1px;
	scale: 0.7;
}

i.none {
	color: var(--gray);
}

i.high {
	color: var(--High-priority);
}

i.medium {
	color: var(--Medium-priority);
}

i.low {
	color: var(--Low-priority);
}

#color-pop-up {
	display: none;
	position: absolute;
	background: var(--light-gray);
}

#color-pop-up.active {
	display: flex;
	padding: 5px 0px;
	justify-content: space-between;
	flex-direction: column;
	border-radius: 10px;
}

#color-pop-up i {
	scale: 0.7;
	cursor: pointer;
	transition: all 100ms ease;
}

#color-pop-up i:hover {
	scale: 0.75;
	transition: all 100ms ease;
}

/* CREATE TODO POP UP */

#to-do-pop-up,
#new-project-pop-up {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	gap: 10px;
	position: absolute;
	width: 700px;
	height: 450px;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	padding: 25px;
	background: var(--white);
	border: 1px solid var(--light-gray);
	z-index: 999;
	border-radius: 15px;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

@media (max-width: 750px) {
	#to-do-pop-up,
	#new-project-pop-up {
		width: 95%;
		height: 70%;
	}

	#to-do-pop-up div:nth-child(1) {
		display: flex;
		gap: 50px;
	}
}

/* TODO'S POP UP INPUTS */

#to-do-pop-up input[type="text"] {
	border: none;
	outline: 0px;
	background-color: inherit;
	padding: 0;
}

#to-do-pop-up select {
	border: none;
	color: var(--black);
	font-weight: 500;
	font-family: "DM Sans", Helvetica, sans-serif;
	padding: 5px 5px;
	border-radius: 5px;
	width: 30%;
	cursor: pointer;
	line-height: 1.1;
	background: var(--light-gray);
}

option {
	text-align: left;
	font-weight: 500;
	background: var(--light-gray);
}

option:hover {
	background: red;
}

#to-do-priority option:nth-of-type(2) {
	color: var(--Low-priority);
}

#to-do-priority option:nth-of-type(3) {
	color: var(--Medium-priority);
}

#to-do-priority option:nth-of-type(4) {
	color: var(--High-priority);
}

/* TODO INPUTS */

#to-do-pop-up input[type="text"]:focus {
	border-bottom: 1px solid var(--gray);
}

#to-do-pop-up div {
	margin: 5px 0px;
}

#to-do-pop-up > div:first-child {
	display: flex;
	justify-content: space-between;
	gap: 20px;
}

#to-do-pop-up div p {
	font-weight: 500;
	font-size: 1rem;
	margin-bottom: 5px;
}

#to-do-pop-up button {
	padding: 3px 20px;
	transition: all 200ms ease;
	border-radius: 5px;
	font-weight: 500;
}

#to-do-pop-up button#save-to-do,
button#save-edited-btn {
	background: var(--black);
	color: var(--white);
}

#to-do-pop-up button#save-to-do:hover,
button#save-edited-btn:hover {
	background-color: var(--light-gray);
	color: var(--black);
	transition: all 200ms ease;
}

button#close-pop-up {
	background: none;
	display: flex;
	height: 35px;
	padding: 0;
	justify-content: center;
	align-items: center;
}

button#close-pop-up:hover {
	background: var(--light-gray);
}

#to-do-title {
	flex: 1;
	color: var(--black);
	font-weight: 600;
	font-size: 2rem;
}

#to-do-pop-up input[type="text"]#to-do-title {
	width: 30px;
}

#to-do-description {
	color: var(--gray);
}

#date-container {
	display: flex;
	gap: 10px;
	align-items: center;
}

#date-container i {
	cursor: pointer;
}

.flatpickr-calendar {
	font-family: "DM Sans", Arial, Helvetica, sans-serif;
}

input#to-do-date.flatpickr-input {
	border: 1px solid #ccc;
	padding: 5px 10px;
	border-radius: 10px;
}

#buttons-container {
	width: 100%;
	display: flex;
	gap: 10px;
	justify-content: space-between;
}

#buttons-container button {
	flex: 1;
}

#buttons-container button#delete-to-do-btn {
	background: var(--High-priority);
	color: white;
}

/* NEW PROJECT POP UP */

#new-project-pop-up {
	display: flex;
	flex-direction: row;
	height: 100px;
	width: 400px;
	align-items: end;
	justify-content: space-between;
}

@media (max-width: 750px) {
	#new-project-pop-up {
		width: 95%;
	}
}

#new-project-pop-up input {
	flex: auto;
	padding: 0px 0px 0px 20px;
	height: 100%;
	font-weight: 600;
	color: var(--black);
	border: none;
	font-size: 1.1rem;
	background: var(--light-gray);
	border-radius: 10px;
}

#new-project-pop-up input:focus {
	outline: 1px solid var(--gray);
}

#new-project-pop-up button {
	padding: 0;
}

#new-project-pop-up button:active {
	scale: 1.1;
}

.project-btn {
	background: #f1f1f1;
	position: relative;
}

.project-btn:hover {
	background: #e9e9e9;
}

.project-btn span {
	line-height: 1;
}

.delete-project-btn {
	position: absolute;
	right: 10px;
	top: 7px;
	color: var(--gray);
}

.delete-project-btn i.material-symbols-rounded {
	font-size: 1.6rem;
}

/* BLURRY BACKGROUND */

#to-do-pop-up-container,
#new-project-pop-up-container {
	position: fixed;
	overflow: hidden;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 0px;
	width: 100%;
	background: rgba(255, 255, 255, 0.19);
	border-radius: 0;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(3px);
	-webkit-backdrop-filter: blur(2.8px);
	z-index: 2;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out,
		height 0.3s ease-in-out;
}

#to-do-pop-up-container.active,
#new-project-pop-up-container.active {
	opacity: 1;
	visibility: visible;
	height: 100%;
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out,
		height 0.3s ease-in-out;
}
